.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.searchInputs {

  display: flex;
}

.search input {
  background-color: rgb(241, 99, 99);
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  max-width: 100vw;
  margin: 10px 30px;
}

input:focus {
  outline: none;
}

.dataResult {
  margin-top: 5px;
  min-width: 600px;
  height: auto;
  background-color: rgb(35, 222, 25);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;


}


.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: rgb(234, 15, 15);
}

#clearBtn {
  cursor: pointer;
}
.result{
  list-style-type:none;
  background-color:blue;
  border: 1px solid rgb(47, 23, 136);
  z-index: 9999999;

}